p {
    margin: 0;
    font-size: 15px;
    margin: 4px 0;
}
body {
    margin: 0;
    background-color: pink;    
}
#login-page {
  padding: 10px 20px;
}
@media (min-width: 768px) {
  #form-login {
    display: flex;
    justify-content: center;
  }
  #form-login div{
    width: 50%;
  }
}
#header {
    position: fixed;
    width: 100%;
    background-color: pink;
    height: 50px;
    padding: 10px 10px 0 10px;
    z-index: 10;
    top: 0;
    border-bottom: 1px solid #f9005a;
}
input{
    width: 100%;
    height: 30px;
    border-radius: 32px;
    border: 1px solid #ccc;
}
input:focus-visible {
    border: 1px solid #ccc;
    outline: #ccc;
}
button {
    height: 30px;
    border: 1px solid #f9005a;
    border-radius: 32px;
    background-color: #f9005a;
    color: white;
    min-width: 100px;
    cursor: pointer;
}
#content {    
    margin-top: 50px;
    padding: 10px;
}
.row-item {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
}
.message {
    display: flex;
    align-items: center;
    padding: 5px 0;
    gap: 10px;
}
.message:hover .datetime{
    display: block;
}
.message-right, .message-right .row-item {
    justify-content: flex-end;
}
.avatar {
    height: 28px;
    width: 28px;
}
.avatar img {
    width: 28px;
    height: 28px;
    border-radius: 28px;
}
.message-text {
    padding-left: 10px;
    background-color: #fff5f5;
    border-color: #fff5f5;
    border-radius: 32px;
    color: black;
    padding: 8px 12px;
    font-size: 16px;
    word-break: break-all;
}
.message-text-right {
    background-color: #f9005a;
    border-color: #f9005a;
    border-radius: 32px;
    color: white;
    padding: 8px 12px;
    font-size: 16px;
    word-break: break-all;
}
.datetime {
    font-size: 10px;
    display: none;
}